html{
  box-sizing: border-box;
  font-size: 62.5%;
}

ul li {
  list-style-type: none;
}

body, #root{
  height: 100vh;
  margin: 0;
}

body, input, select, textarea{
  font-size: 1.6rem;
  font-family: Helvetica, Arial, sans-serif;
}


a{
  text-decoration: none;
}

.grid-container{
  display: grid;
  grid-template-areas: "header" "main" "footer";
  grid-template-rows: 5rem 1fr 5rem;
  grid-template-columns: 1fr;
  height: 100%;
}

/*HEADER STARTS HERE*/

header{
  grid-area: header;
  background-color: #203040;
  color: #ffffff;
  display:flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}

header a{
  color: #ffffff;
}

header a:hover {
  color: #ff8000;
}

.brand{
  font-size: 2.5rem;
}

.header-links a:last-child{
  padding:0 1rem;
}


/*HEADER ENDS HERE*/

/*SIDE BAR STARTS HERE*/
.brand button{
  font-size: 3rem;
  padding: 0.5rem;
  background: none;
  border: none;
  color: #ffffff;
  cursor: pointer;

}

.sidebar{
  position: fixed;
  transition: 1s;
  transform: translateX(-30rem);
  width: 25rem;
  background-color: #f0f0f0;
  height: 100%;
}

.open{
  transform: translateX(0);
}

.sidebar-close-btn{
  border-radius: 50%;
  border: 0.1rem #000000 solid;
  width: 3rem;
  height: 3rem;
  padding: 0.5rem;
  font-size: 2rem;
  padding-top: 0.25;
  cursor: pointer;
  position: absolute;
  right: 0.5rem;
  top: 1.5rem;
}

.sidebar-close-btn:hover{
  background-color: #ff8000;
}

.sidebar ul {
  padding: 1rem;
}

.sidebar li {
  border-bottom: 1px solid #000000;
  padding: 2rem 0.1rem;
}

.sidebar h3{
  padding-left: 0.5rem;
}
/*SIDE BAR ENDS HERE*/

/*MAIN STARTS HERE*/

main{
  grid-area: main;
}


/*PRODUCTS STARTS HERE*/
 .products{
   display: flex;
   justify-content: center;
   align-items: center;
   flex-wrap: wrap;
 }

 .product{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

 .products li{
   flex: 0 1 34rem;
   margin: 1rem;
   height: 50rem;
   border-bottom: 0.1rem #c0c0c0 solid;
   list-style-type: none;
   padding: 0;
 }

 .product-image{
   min-width: 30rem;
   max-width: 34rem;
   max-height: 34rem;
 }
 
 .product-image img {
  margin: auto;
  padding: auto;
 }

 .product-name{
   font-size: 2rem;
   font-weight: bold;
 }

  .product-brand{
  font-size: 1.2rem;
  color: #808080;
}

.product-price{
  font-size: 2rem;
  font-weight: bold;
}


.product-rating{
  margin-bottom: 1rem;
}

main a:hover{
  color: #ff8000;
}


/*PRODUCTS ENDS HERE*/


/*MAIN ENDS HERE*/

footer{
  grid-area: footer;
  background-color: #203040;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}

